import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Terminal } from 'xterm';
import * as attach from 'xterm/src/addons/attach/attach';
import * as fit from 'xterm/src/addons/fit/fit';

@Component({
  selector: 'app-terminal',
  templateUrl: './terminal.component.html',
  styleUrls: ['./terminal.component.css']
})
export class TerminalComponent implements OnInit {

  terminal: any;
  @ViewChild('terminal', { static: true }) term: ElementRef;

  ngOnInit() {
    Terminal.applyAddon(attach);
    Terminal.applyAddon(fit);
    this.terminal = new Terminal({
      cols: 140,
      rows: 29,
      cursorBlink: true,
      fontSize: 16,
      fontFamily: 'Menlo, Monaco, Consolas, monospace',
    });
    this.terminal.open(this.term.nativeElement);
    const socketURL = "wss://" + window.location.hostname + ":8080/ws"
    const socket = new WebSocket(socketURL);
    socket.onopen = () => {
      this.terminal.attach(socket);
      this.terminal._initialized = true;
    }
    const actualCellsize = {
      height: this.term.nativeElement.offsetHeight / this.terminal.getOption('rows'),
      width: this.term.nativeElement.offsetWidth / this.terminal.getOption('cols')
    }
  }

}
